<template>
    <section>
      <transition name="result">
      <div class="weather-card" @click="saveWeatherData(weatherData)"
      v-if="showInfo">
        <div class="card-header">
          <h1>{{ weatherData.city }}, {{ weatherData.country }}</h1>
        </div>
        <div class="card-body">
          <i :class="weatherData.icon"></i>
          <p>{{ weatherData.forecast }}</p>
        </div>
        <div class="card-footer">
          <h1 v-html="`${weatherData.temp}&deg;`"></h1>
        </div>
        <div class="weather-card-modal">
          <h3>Save</h3>
        </div>
      </div>
      </transition>
    </section>
</template>

<script>
    export default {
        
        computed: {
            showInfo() {
                return this.$store.state.showInfo;
            },
            weatherData() {
                return this.$store.state.weatherData;
            }
        },

        methods: {

            saveWeatherData(data) {
                data.bgColor = this.setRandomColor();
                this.$store.state.cities.push(data);
                this.$store.state.query = '';
                this.$store.state.showInfo = false;
            },

            setRandomColor() {
                var letters = 'BCDEF'.split('');
                var color = '#';
                for (var i = 0; i < 6; i++ ) {
                    color += letters[Math.floor(Math.random() * letters.length)];
                }
                return color;
            }
        }

    }
</script>

<style scoped>
    .weather-card {
        width: 170px;
        height: 270px;
        padding: 1em;
        background-color: #F2B900;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        position: relative;
        overflow: hidden;
        cursor: pointer;
        box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
    }
    .weather-card-modal {
        position: absolute;
        top: 100%;
        left: 0;
        z-index: 20;
        width: 100%;
        height: 100%;
        background: rgba(12,201,43,0.6);
        display: flex;
        justify-content: center;
        align-items: center;
        color: #FFF;
        transition: top .3s;
    }
    .weather-card:hover > .weather-card-modal {
        top: 0;
    }
    .card-header h1 {
        font-size: 1.2em;
    }
    .card-body p {
        text-transform: uppercase;
        font-size: .7em;
    }
    .card-body i {
        font-size: 3em;
    }
    .card-footer h1 {
        font-size: 1.8em;
    }
</style>


