<template>
  <header>
      <input @keyup="getCities($event.target.value)" type="text" v-model="query">
      <div id="results">
        <ul v-if="showResults">
          <li v-for="city in cityResults" :key="city.name" @click="$store.dispatch('fetchWeather', city.name)">
            {{ city.name }}
          </li>
        </ul>
      </div>
    </header>
</template>

<script>

import { Observable } from 'rxjs/Observable';
import 'rxjs/add/observable/from';
import 'rxjs/add/operator/filter';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/distinctUntilChanged';

export default {
  
  computed: {
    query: {
      get() {
        return this.$store.state.query;
      },
      set(value) {
        this.$store.state.query = value;
      }
      
    },
    showResults() {
      return this.$store.state.showResults;
    },
    cityResults() {
      return this.$store.state.cityResults;
    }
  },

  methods: {

    getCities(search) {
      let obs = Observable.create((observer) => {
        observer.next(search);
      });
      obs
        .filter(text => text.length > 2)
        .debounceTime(200)
        .distinctUntilChanged()
        .subscribe((query) => this.$store.dispatch('getCities', query));
    }

  }
  
}
</script>

<style scoped>
  header {
    width: 400px;
    padding: 1em;
    position: relative;
  }
  header input {
    width: 100%;
    height: 20px;
    text-indent: 5px;
  }
  #results {
    width: 93%;
    position: absolute;
    z-index: 10;
  }
  #results ul {
    text-align: left;
    margin: 0;
  }
  #results li {
    padding: 5px;
    background-color: #FCF2E3;
  }
  #results li:hover {
    background-color: #F7E8D7;
    cursor: pointer;
  }
</style>



