<template>
  <div id="app" class="container">
    <h1>Weather Cards</h1>
    <p>Search by city and save/remove cards to local storage</p>
    <search-results></search-results>
    <search-card></search-card>
    <weather-board></weather-board>
    <footer>
      <p>Built with 
        <a href="https://vuejs.org/">VueJS</a>, 
        <a href="https://vuex.vuejs.org/en/">Vuex</a>, 
        <a href="http://reactivex.io/">RxJS</a>, 
        <a href="https://github.com/mzabriskie/axios">Axios</a>, and the 
        <a href="https://openweathermap.org/api">OpenWeatherMap API</a></p>
    </footer>
  </div>
</template>

<script>

  import SearchResults from './components/SearchResults';
  import SearchCard from './components/SearchCard';
  import WeatherBoard from './components/WeatherBoard';
  import axios from 'axios';

export default {
  name: 'app',
  components: {
    'search-results': SearchResults,
    'search-card': SearchCard,
    'weather-board': WeatherBoard
  }
}
</script>

<style>

  /* 
    sun: #F2B900
   */

  @import url('https://fonts.googleapis.com/css?family=Lato');
  @import url('../static/css/open-weather-icons.css');

  #app {
    font-family: 'Lato', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
  }

  body {
    margin: 0;
    padding: 0;
    background-color: #FFFAF4;
  }

  h1, h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  .container {
    padding: 1em;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
  }

  footer {
    padding: 2em;
  }
  footer a {
    color: #B55A00;
    text-decoration: none;
  }

</style>



