<template>
    <section>
      <div class="weather-board" v-if="cities.length > 0">
        <transition-group name="list" tag="ul" class="list-ul">
          <li v-for="(item,index) in cities" :key="item.city">
            <div class="weather-card" @click="removeWeatherData(index)" :style="{backgroundColor: item.bgColor}">
              <div class="card-header">
                <h1>{{ item.city }}, {{ item.country }}</h1>
              </div>
              <div class="card-body">
                <i :class="item.icon"></i>
                <p>{{ item.forecast }}</p>
              </div>
              <div class="card-footer">
                <h1 v-html="`${item.temp}&deg;`"></h1>
              </div>
              <div class="weather-card-modal delete">
                <h3>Remove</h3>
              </div>
            </div>
          </li>
        </transition-group>
      </div>
      <div id="message" v-else>
        <h3>No weather items to show.<br> Add an item using the search box above</h3>
      </div>
    </section>
</template>

<script>
    const storage = require('../../static/storage');

    export default {
        computed: {
            cities: {
                get() {
                    return this.$store.state.cities;
                },
                set(value) {
                    this.$store.state.cities = value;
                }
            }
        },
        methods: {

            removeWeatherData(index) {
                this.cities.splice(index, 1);
            },

            refreshForecast() {
                let cities = storage.getCities();
                cities.forEach(item => {
                    this.$store.dispatch('refreshForecast', item);
                });
            }
        },

        created() {
            if(storage.checkStorage()){
                this.cities = storage.getCities();
            } else {
                this.cities = [];
            }
        },
        
        updated() {
            this.refreshForecast();
            storage.saveCities(this.cities);
        }
    }
</script>

<style scoped>
    .weather-card {
        width: 170px;
        height: 270px;
        padding: 1em;
        background-color: #F2B900;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        position: relative;
        overflow: hidden;
        cursor: pointer;
        box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
    }
    .weather-card-modal {
        position: absolute;
        top: 100%;
        left: 0;
        z-index: 20;
        width: 100%;
        height: 100%;
        background: rgba(12,201,43,0.6);
        display: flex;
        justify-content: center;
        align-items: center;
        color: #FFF;
        transition: top .3s;
    }
    .delete {
        background: rgba(255,12,49,0.6);
    }
    .weather-card:hover > .weather-card-modal {
        top: 0;
    }
    .card-header h1 {
        font-size: 1.2em;
    }
    .card-body p {
        text-transform: uppercase;
        font-size: .7em;
    }
    .card-body i {
        font-size: 3em;
    }
    .card-footer h1 {
        font-size: 1.8em;
    }

    .weather-board {
        width: 100%;
        max-width: 800px;
        padding: 1em;
    }
    .weather-board ul {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-gap: 20px;
    }

    /* result transition */

    .result-enter-active,
    .result-leave-active {
        transition: opacity .7s, transform .7s;
    }
    .result-enter,
    .result-leave-to {
        opacity: 0;
        transform: translateY(-10px);
    }

    /* weatherboard transition */
    .list-ul {
        transition: all .7s;
    }
    .list-enter-active,
    .list-leave-active {
        transition: opacity .7s, transform .7s;
    }
    .list-enter,
    .list-leave-to {
        opacity: 0;
        transform: translateY(-10px);
    }
</style>


