import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';

Vue.use(Vuex);

export default new Vuex.Store({

    state: {
        apikey: '579dc9e1b9185e8312a5973983e5b62e',
        query: '',
        showResults: false,
        showInfo: false ,
        weatherData: {},
        cityResults: [],
        cities: [],
        refresh: false
    },

    mutations: {

       GET_CITIES(state, payload) {
        state.showResults = true;
        state.cityResults = payload;
       },

       FETCH_WEATHER(state, payload) {
        state.weatherData = {
            id: payload.id,
            city: payload.name,
            country: payload.sys.country,
            temp: Math.round(((payload.main.temp - 275.15) * 1.8)),
            forecast: payload.weather[0].description,
            icon: 'owi owi-' + payload.weather[0].icon
          }
          state.showResults = false;
          state.showInfo = true;
          state.query = payload.name;
       },

       REFRESH_FORECAST(state, city) {
            for(let i = 0; i < state.cities.length; i++) {
                if(state.cities[i].id === city[1]) {
                    state.cities[i].temp = Math.round(((city[0].main.temp - 275.15) * 1.8));
                    state.cities[i].forecast = city[0].weather[0].description;
                    state.cities[i].icon = 'owi owi-' + city[0].weather[0].icon;
                    break;
                }
            }
       }
    },

    actions: {

        getCities({commit}, q) {
            return axios
            .get('http://autocomplete.wunderground.com/aq?query=' + q)
            .then(res => commit('GET_CITIES', res.data.RESULTS))
            .catch(error => console.log(error))
        },

        fetchWeather({state, commit}, cityName) {
            return axios({
              method: 'get',
              url: 'http://api.openweathermap.org/data/2.5/weather',
              params: {
                q: cityName,
                APPID: state.apikey
              }
            })
            .then((res) => commit('FETCH_WEATHER', res.data))
            .catch((error) => console.log(error));
          },

          refreshForecast({state, commit}, item) {
            return axios({
                    method: 'get',
                    url: 'http://api.openweathermap.org/data/2.5/weather',
                    params: {
                        q: item.city,
                        APPID: state.apikey
                    }
                })
                .then(res => {
                    commit('REFRESH_FORECAST', [res.data, item.id]);
                })
                .catch((error) => console.log(error));
          }

    }

});


